export default {
	data() {
		return {
			phone: '',
			rTime: '获取验证码',
			interval: null,
			btnState: false,
			YZM: '',
			code: '',
			interCode: '',
			uid: '',
			showBind: true
		};
	},
	onLoad(options) {
		getApp().globalData.uid=options.uid;
		if (options.phoneNum=='null') {
			this.showBind = true;
		} else {
			this.showBind = false;
			uni.switchTab({
				url: '/pages/tabbar/home'
			});
		}
	},
	methods: {
		getYZM() {
			if (this.phone == '') {
				this.$api.tip('手机号不能为空!');
			} else if (!this.$api.regPhone(this.phone)) {
				this.$api.tip('请输入正确的手机号!');
			} else {
				if (this.rTime == '获取验证码') {
					this.rTime = 60;
					this.btnState = true;
					this.interval = setInterval(() => {
						if (--this.rTime <= 0) {
							this.rTime = '获取验证码';
							clearInterval(this.interval);
							this.btnState = false;
						}
					}, 1000);
					let parmas = {
						phoneNum: this.phone
					};
					this._reqw
						.ipost(parmas, 'sendSmsCode')
						.then(res => {
							res.result == 0 ? ((this.Code = res.code), this.$api.tip(res.resultNote)) : this.$api.tip(res.resultNote);
						})
						.catch(err => {});
				}
			}
		},
		subOrder() {
			uni.showLoading({
				title: '登录中...'
			});
			// let Y = this.YZM.trim();
			if (this.phone == '') {
				this.$api.tip('手机号不能为空!');
			} else if (!this.$api.regPhone(this.phone)) {
				this.$api.tip('请输入正确的手机号!');
			} else if (this.YZM == '') {
				this.$api.tip('请输入验证码');
			} else {
				let parmas = {
					phone: this.phone,
					uid: getApp().globalData.uid,
					code: this.YZM,
					inverteCode: this.inverteCode
				};
				console.log(parmas);
				this._reqw
					.ipost(parmas, 'bindPhone')
					.then(res => {
						res.result == 0 ?
							(uni.hideLoading(),
								this.$api.tip(res.resultNote),
								(getApp().globalData.uid = this.uid),
								setTimeout(() => {
									uni.switchTab({
										url: '/pages/tabbar/home'
									});
								}, 300)) :
							this.$api.tip(res.resultNote);
					})
					.catch(err => {});
			}
		}
	},
	onHide() {
		if (this.interval) {
			clearInterval(this.interval);
		}
	}
};
